.quotationtable {
  margin: 10px auto 0;
  border-radius: 0 0 4px 4px;
  z-index: 9;
  position: relative;
  padding-bottom: 80px !important;
  overflow: visible !important;

  tbody tr:last-child td {
    border-bottom: 1px solid #dedede !important;
  }

  tfoot tr td {
    padding: 10px 17px !important;
  }

  th {
    padding: 0px 17px !important;
    border-bottom: 1px solid #dedede !important;
  }

  
}

.quotationfooter {
  z-index: 10;
  width: 100%;
  position: fixed;
  bottom: 0;
  background: #fff;
  box-shadow: 0px -2px 5px 0px rgba(16, 24, 40, 0.1),
    0px 1px 2px 0px rgba(16, 24, 40, 0.06);
  .contanerfooter {
    width: 1024px;
    margin: 0 auto;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 16px 0px;
    .right {
      display: flex;
    }
    button {
      border-radius: var(--radius-xs, 4px);
      border-radius: var(--radius-xs, 4px);
      border: 1px solid var(--Gray-300, #d0d5dd);
      background: #fff;
      display: flex;
      padding: var(--spacing-lg, 12px) var(--spacing-2xl, 20px);
      justify-content: center;
      align-items: center;
      color: var(--Gray-700, #344054);
      font-size: 16px;
      font-style: normal;
      font-weight: 600;
      line-height: 24px;

      &.decline {
        border: 1px solid var(--Error-200, #fecdca);
        background: var(--Error-50, #fef3f2);
        color: var(--Error-700, #b42318);
      }
      &.accept {
        border: 1px solid var(--Success-600, #079455);
        background: var(--Success-600, #079455);
        color: #fff;
        margin-left: 12px;
      }
    }
  }
}

